import { Margins } from "../styleguide";

export interface StyledSingleBreadCrumbProps extends Margins {
	$isActive?: boolean;
}

export interface SingleBreadCrumbProps extends StyledSingleBreadCrumbProps {
	dataQA: string
	href: string;
	ariaLabel: string;
	className?: string;
	breadCrumbText: string
	expandBreadcrumbs?: boolean;
}

export interface ExtendedSingleBreadCrumbProps extends SingleBreadCrumbProps {
	breadcrumbIndex: number
	breadCrumbsLength: number
}

export interface BreadCrumbsProps extends Margins {
	dataQA: string;
	ariaLabel: string;
	className?: string;
	children?: React.ReactNode | JSX.Element[];
}

